import React from 'react';
import './Statistics.css';
import CountUp from 'react-countup';


function Statistics() {
  return (
    <div className='container'>
      <div className='statistics' id='statistics'>
        <ul className='statistics__list'>
          <li className='statistics__item'>
            <span className='statistics__caption'></span> 
            <CountUp className='statistics__title font__family_crystal' start={0} end={9} separator=' ' />
            <p className='statistics__subtitle'><span className='font__weight_bold'>  </span></p>
          </li>
          <li className='statistics__item'>
            <span className='statistics__caption'>  2022/2023 </span>
            <CountUp className='statistics__title font__family_crystal' start={0} end={1212} separator=' ' />
            <p className='statistics__subtitle'><span className='font__weight_bold'></span> </p>
          </li>
          <li className='statistics__item'>
            <span className='statistics__caption'>  2023/2024 </span>
            <CountUp className='statistics__title font__family_crystal' start={0} end={2548} separator=' ' />
            <p className='statistics__subtitle'><span className='font__weight_bold'></span> </p>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Statistics;
